import { Component, Element, h, Host, Prop } from '@stencil/core';

let id = 0;

@Component({
	tag: 'o-tab-panel',
	styleUrl: 'o-tab-panel.scss',
	shadow: true
})
export class OTabPanel {
	private componentId = `tab-panel-${++id}`;

	@Element() host!: HTMLOTabPanelElement;

	/** The tab panel's name. */
	@Prop() name = '';

	/** When true, the tab panel will be shown. */
	@Prop({ reflect: true }) active = false;

	render() {
		return (
			<Host>
				<div id={this.host.id || this.componentId} class={this.active ? 'block' : 'hidden'}>
					<div class="text-sm text-main_text py-9" role="tabpanel">
						<slot />
					</div>
				</div>
			</Host>
		);
	}
}
